.header {
  height: 85vh;
  width: 100%;
  background-image:url(../images/bg-0-s.jpg);
  background-position: center bottom;
  background-size: cover;
  position: relative;
  overflow: hidden;
  transition: filter 1.5s;
  filter: brightness(1.5);
  &.on {
    filter: brightness(1);
  }
  @include media-breakpoint-up(sm) {
    background-image:url(../images/bg-0.jpg);
    height: 70vh;
  }
  @include media-breakpoint-up(lg) {
    max-height: 870px;
    height: 95vh;
  }

  //手機橫式
  @media (max-height: 414px) {
    height: 100vh;  
  }
}
.fancy-menu {
  position: absolute;
  top: 0.5em; 
  right: 0.5em;
  z-index: 10;
  display: inline-block;
  background-color: $primary;
  span {
    background: $gray-100;
    border-radius: 3px;
    display: block;
    height: 2px;
    margin-top: 5px;
    padding: 0;
    position: relative;
    transition: all 0.2s ease;
    width: 25px;
    &:last-child {
      margin-bottom: 5px;
    }
  }
  &.active {
    span {
      &:nth-of-type(1) {
        transform: rotate3d(0, 0, 1, 45deg);
        top: 7px;
      }
      &:nth-of-type(2) {
        opacity: 0;
      }
      &:nth-of-type(3) {
        transform: rotate3d(0,0,1,-45deg);
    top: -7px;
      }
    }
  }
}
.navbar {
  position: fixed;
  width: 100%;
  padding: 0;
  z-index: 5;
  transition: all .5s;
  transform: translateY(-50%);
  opacity: 0;
  &.on {
    transform: translateY(0);
    opacity: 1;
  }
  &-collapse {
    position: absolute;
    top: 0;
    bottom: 0;
    height: 100vh;
    width: 100%;
    background-color:white;
    opacity: 0;
    transition: all .3s;
    &.show {
      opacity: 1;
    }
  }
  &-nav {
    height: 100vh;
    justify-content: center;
    align-items: center;
    .nav-link.nav-link {
      color: $primary;
      font-size: 1.25em;
      letter-spacing: 5px;  
      padding: .25em .5em; 
      text-indent: 5px; 
      margin-bottom: .5em; 
      margin-top: 0.5em;
      position: relative;
      span {
        position: absolute;
        margin: auto;
        left: 0;
        right: 0;
        display: block;
        width: 5px;
        height: 5px;
        background-color: $primary;
        transition: all .3s;
        opacity: 0;
        bottom: -8px;
        transform: rotate(45deg);
      }
      &::before,&::after {
        position: absolute;
        content: " ";
        transition: all .3s;
        background-color: $secondary;
        height: 1px;
        width: 0%;
        display: block;
        margin: auto;
        left: 0;
        right: 0;
        bottom: 0;
      }
      &::before {
        transform: translateX(-100%);
      }
      &::after {
        transform: translateX(100%);
      }
      &:hover { 
        span {
          opacity: 1;
          bottom: -2.5px;
        }
        &::before,&::after {
          width: 25%;
        }
      }
    }
  }
  &-brand {
    display: none;
  }
  &.fixed {
    opacity: 1;
    transform: translateY(0);
  }
  @include media-breakpoint-up(lg) {
    padding-left: 2em;
    padding-right: 2em;
    &-collapse {
      justify-content: flex-end;
      position: relative;
      width: auto;
      height: auto;
      background-color: rgba($color: white, $alpha: 0);
      opacity: 1;
    }
    &-nav {
      height: auto;
      /*li*/.nav-item {
        margin-left: 1em;
        &:first-child{
          margin-left: 0;
        }
        &:last-child {
          a.nav-link {
            font-size: 0.9em;
            letter-spacing:2px; 
          }
        }
      }
      a.nav-link.nav-link {
        font-size: 1.05em;
        margin-top: 1em;
        margin-bottom: 1em;
        // filter: drop-shadow(0px 0px 2px white) drop-shadow(0px 0px 2px white);
      }
      
    }
    &-brand {
      display: block;
      width: 35%;
      max-width: 200px;
      padding: 0.5em;
      transition: all .3s ease ;
      img {
        max-width: 100%;
        height: auto;
      }
    }
    &.fixed {
      background-color: rgba(white,.3);
      .navbar-brand {
        max-width: 100px;
      }
      &:hover {
        background-color: rgba(white,.9);
      }
    }
  }
}
