$color-sound:#77e5c9;
$size: 35px;
$border: $size/15;
.sound {
  width: $size;
  height: $size;
  position: absolute;
  bottom: 0;
  left: 1em;
  cursor: pointer;
  z-index: 1000;
  list-style: none;
  &--icon {
    position: absolute;
    margin: auto;
    left: 5px;
    top: 0;
    bottom: 0;
  }

  &--wave {
    position: absolute;
    border: $border solid transparent;
    border-right: $border solid $color-sound;
    border-radius: 50%;
    transition: all 200ms;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    &_one {
      width: 50%;
      height: 50%;
    }

    &_two {
      width: 75%;
      height: 75%;
    }
  }

  &-mute .sound--wave {
    border-radius: 0;
    width: 50%;
    height: 50%;
    border-width: 0 $border 0 0;

    &_one {
      transform:
        rotate(45deg) 
        translate3d(0, -50%, 0);
    }

    &_two {
      transform: 
        rotate(-45deg)
        translate3d(0, 50%, 0);
    }
  }
}
