.tit-text {
  transform: translateY(-45%);
  max-width: 1140px;
  width: 100%;
  // margin-left: -15px;
  margin-left: auto;
  margin-right: auto;
  h3 {
    font-family: 'Cormorant Garamond', serif;
    font-size: 2.25em;
    text-transform: uppercase;
    display: inline-block;
    position: relative;
    &::before, &::after {
      position: absolute;
      content: "";
      margin: auto;
      top: 0;
      bottom: 0;
      width: 2em;
      height: .4em;
    }
    &::after {
      background: url(../images/arw.svg) center center no-repeat;
      background-size: auto 100%;
    }
  }
  &.both {
    h3 {
      &::before {
        transform: translateX(-100%);
