    }
  }
  @include media-breakpoint-up(lg) {
    h3 {
      font-size: 3.75em;
    }
  }
}
.btn-act {
  position: relative;;
  display: inline-block;
  color: white;
  background-color: $secondary;
  padding: 1em 1.5em;
  transition: all .3s;
  &::before {
    content: '';
    position: absolute;
    // display: inline-block;
    width: 100%;
    height: 100%;
    top: 0.3em;
    left: 0.2em;
    border: 1px solid $secondary;
    transition: all .3s;
  }
  &:hover {
    text-decoration: none;
    color: white;
    background-color: darken($secondary, 10);
    &::before {
      top: 0.45em;
      left: 0.35em;
      // border-color:lighten($primary,  5);
    }
  }
}
.nav {
  // margin-bottom: 2em;
  font-size: .85em;
  .nav-item {
    margin-left: 1em;
    border-bottom: 1px solid $primary;
    position: relative;
    &::after {
      position: absolute;
      display: block;
      content: '';
      width: 0;
      margin: auto;
      // left: 0;
      // right: 0;
      top: 0;
      bottom: 0;
      background-color:rgba(255, 255, 255, .5);
      z-index: 0;
      transition: width .3s;
    }
    &:hover {
      &::after {
        width: 100%;
      }
    }
    &:first-child {
      margin-left: 0;
    }
  }
  .nav-link {
    display: block;
    position: relative;
    z-index: 1;
    &::before,&::after {
      position: absolute;
      content: '';
      height: 6px;
      width: 1px;
      background-color: $primary;
      bottom: -3px;
    }
    &::before {
      left: 0;
    }
    &::after {
      right: 0;
    }
    &:hover {
      
    }
    &.active {
      color: white;
      background-color: $primary;
    }
  }
  @include media-breakpoint-up (sm) {
    font-size: 1em;
    margin-left: 2.5em;
    margin-right: 2.5em;
  }
}
.news {
