    border-bottom: 1px solid $primary;
    padding: 1.5em .5em;
    display: flex;
    align-items: center;
    &:last-child {
      border-bottom: none;
    }
    &:nth-last-child(n+2) {
      &::before,&::after {
        position: absolute;
        content: '';
        height: 6px;
        width: 1px;
        background-color: $primary;
        bottom: -3px;
      }
      &::before {
        left: 0;
      }
      &::after {
        right: 0;
      }
    }
  }
  small {
    font-family: 'Noto Serif TC';
    font-size: 1em;
    margin-left: auto;
  }
  a {
    text-decoration: none;
  }
}
