.character {
  display: flex;
  flex-direction: column;
  padding-bottom: 2em;
  dd {
    flex-grow: 1;
    height: 20vh;
    min-height: 135px;
    position: relative;
    background-color: $light;
    cursor: pointer;
    overflow: hidden;
    margin-bottom: 1px;
    transition: all 0.3s;
    .character_img {
      transition: all .5s;
      &:nth-child(2) {
        // display: none;
        opacity: 0;
        transform: translateX(50%);
      }
    }
    .character_text {
      transition: all .5s;
      opacity: 0;
    }
    &.on {
      flex-grow: 3;
      height: 50vh;
      min-height: 350px;
      .character_img {
        &:nth-child(1) {
          opacity: 0;
          transform: translateX(50%);
        }
      }
      .character_text {
        opacity: 0;
        transform: translateX(-50%);
      }
    }
    &.open-active {
      .character_img {
        &:nth-child(2) {
          opacity: 1;
          transform: translateX(0);
        }
      }
      .character_text {
        opacity: 1;
        transform: translateX(0);
      }
    }
    &:hover {
      .character_img {
        transform: scale(1.1);
        // filter: brightness(1.1);
      }
    }
  }
  @include media-breakpoint-up(lg) {
    flex-direction: row;
    dd {
      height: 775px;
      &.on {
        height: 775px;
      }
    }
  }
  &_text {
    position: absolute;
    bottom: 0;
    height: 40%;
    width: 80%;
    background-image: linear-gradient(90deg, rgba(white, 1) 0%, rgba(white, .85) 50%, rgba(white, 0) 100%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 2em;
    * {
      width: 85%;
      max-width: 350px;
    }
    h2,h6 {
      font-family: 'Noto Serif TC';
    }
    h2 {
      font-weight: bold;
      font-size: 2.25em;
      border-bottom: 1px solid #000;
      margin-bottom: 0.5em;
      padding-bottom: 0.25em;
      small {
        font-weight: bold;
        font-size: 0.35em;
        margin-left: 0.5em;
      }
    }
    h6 {
      margin-bottom: 1.5em;
    }
    p {
      display: none;
    }
  }
  &_img {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center 25%;
    background-size: cover;
    // transition: transform .3s;
    // &:hover {
    //   transform: scale(1.05);
    // }
    &.p1 {
      background-image: url(../images/p-s-1.png);
    }
    &.p2 {
      background-image: url(../images/p-s-2.png);
    }
    &.p3 {
      background-image: url(../images/p-s-3.png);
    }
    &.p4 {
      background-image: url(../images/p-s-4.png);
    }
    &.p5 {
      background-image: url(../images/p-s-5.png);
    }
    &.p1-b {
      background-image: url(../images/p-b-1.png);
    }
    &.p2-b {
      background-image: url(../images/p-b-2.png);
    }
    &.p3-b {
      background-image: url(../images/p-b-3.png);
    }
    &.p4-b {
      background-image: url(../images/p-b-4.png);
    }
    &.p5-b {
      background-image: url(../images/p-b-5.png);
    }
  }
  &_name {
    position: absolute;
    bottom: 0;
    right: 1em;
    z-index: 2;
    p {
      position: relative;
      color: white;
      background-color: $black;
      font-size: 1.25em;
      padding: .5em 1.5em;
      &::before {
        position: absolute;
        content: '';
        margin: auto;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: calc(100% - 10px);
        height: calc(100% - 10px);
        border: 1px solid $light;
      }
    }
  }
  @include media-breakpoint-up(sm) {
    margin-bottom: 0;
    &_img {
      &:nth-child(1) {
        // position: relative;
        background-size: 75% auto;
        background-position-x: 0;
        &::after {
          position: absolute;
          content: '';
          width: 50%;
          right: 0;
          top: 0;
          bottom: 0;
          background-image: linear-gradient(90deg, rgba($light, 0) 0%, rgba($light, 0.85) 30%, rgba($light, 1) 40%);
        }
      }
    }
  }
  @include media-breakpoint-up(md) {
    &_text {
      top: 0;
      height: 100%;
      width: 60%;
      * {
        width: 75%;
      }
      h2 {
        font-size: 3em;
      }
      p {
        display: block;
      }
    }
  }
  @include media-breakpoint-up(lg) {
    bottom: 1.5em;
    right: 0;

    &_img  {
      background-position: center center;
      &:nth-child(1) {
        background-size: cover;
        background-position-x: center;
        &::after {
          display: none;
        }
      }
    }
    &_name {
      p {
        writing-mode: vertical-rl;
        font-size: 1.75em;
        padding: 1em .25em;
      }
    }
  }
}

