.btn-top {
  font-size: 0.85em;
  font-family: 'Cormorant Garamond', serif;
  position: fixed;
  right: -1.5em;
  bottom: -1em;
  opacity: 0;
  transform: translateY(-100%) rotate(90deg);
  transition: all .3s;
  &.on {
    opacity: 1;
    bottom: 1em;
  }
  &:hover {
    bottom: 1.5em;
    text-decoration: none;
  }
  @include media-breakpoint-up(sm) {
    font-size: 1em;
    right: -1em;
  }
}

